import React from 'react';
import { Carousel } from 'primereact/carousel';
import { useNavigate } from 'react-router-dom';
import { GuideCard } from '../GuideCard';
// import styles from './CarouselCustomGuides.module.css';

export default function CarouselCustomGuides({ carouselItems }) {
  const navigate = useNavigate();

  const responsiveOptions = [
    {
      breakpoint: '1900px',
      numVisible: 4,
      numScroll: 3,
    },
    {
      breakpoint: '1668px',
      numVisible: 3,
      numScroll: 2,
    },
    {
      breakpoint: '1400px',
      numVisible: 2,
      numScroll: 2,
    },
    {
      breakpoint: '680px',
      numVisible: 1,
      numScroll: 1,
    },
  ];

  const carouselTemplate = item => {
    const handleItemClick = e => {
      e.preventDefault(); // Prevent default anchor behavior

      if (item.action === 'scroll' && item.target) {
        const element = document.querySelector(item.target);
        if (element) {
          element.scrollIntoView({ behavior: 'smooth', block: 'start' });
        }
      } else if (item.link) {
        navigate(item.link);
      }
    };

    return <GuideCard item={item} onClick={handleItemClick} />;
  };

  return (
    <Carousel
      value={carouselItems}
      itemTemplate={carouselTemplate}
      responsiveOptions={responsiveOptions}
      numVisible={4}
      numScroll={3}
      circular
    />
  );
}
