import React from 'react';
import styles from './GuideCard.module.css'; // Import CSS module for the GuideCard component
import { NavLink } from 'react-router-dom';
import 'primeicons/primeicons.css';

export default function GuideCard({ item, onClick }) {
  const defaultImage = 'path/to/default/image.jpg'; // Provide the path to your default image

  return (
    <>
      <div className={styles.guideCard}>
        <img
          src={item.src || defaultImage}
          alt={item.caption || 'Guide image'}
          className={styles.guideImage}
        />
        <div className={styles.guideCaption}>
          <h3 className={styles.guideTitle}>{item.title || 'Guide Title'}</h3>
          <h4 className={styles.guideSubtitle}>
            {item.subtitle || 'Subtitle'}
          </h4>
          <div className={styles.linkDiv}>
            <NavLink
              href={item.link || '#'}
              onClick={onClick}
              className={styles.link}
            >
              Read now
              <i
                className='pi pi-arrow-right accent '
                style={{ marginTop: '4px', marginLeft: '6px' }}
              />{' '}
            </NavLink>{' '}
            {/* PrimeIcons right arrow */}
          </div>
        </div>
      </div>
      <p className={styles.guideDescription}>
        {item.description || 'Short description of the guide.'}
      </p>
    </>
  );
}
